// 라이브러리
import { useState } from 'react';
import { Tab, Nav } from 'react-bootstrap';
import { Link, Route, Switch } from 'react-router-dom';

// 이미지
import user from './images/icon/user.png';
import search from './images/icon/search.png';
import main from './images/item/main.jpg';

//CSS
import './index.css';
import './App.scss';

//JS
import data from './data.js';
import Detail from './Detail.js';



function App() {

  let [item, changeItem] = useState(data);
  let [searchKeyword, changeSearchKeyword] = useState(false);
  let [findItem, changeFindItem] = useState('');

  return (
    <div className="App">
      <Switch>
        <Route exact path="/">
          <div className="header">
            <span>Table order</span>

            <div className="top_wrap">
              <div className="top_btn" onClick={(e) => {
                changeSearchKeyword(!searchKeyword)
              }}>
                <img className="top_img" src={search}></img>
              </div>
              <div className="top_btn">
                <img className="top_img" src={user}></img>
              </div>
            </div>
            {
              searchKeyword === true ?
                <div className="search_form">
                  <div className="cover"></div>
                  <div className="search_inp_wrap">
                    <input className="search_inp" placeholder="메뉴를 검색하세요." onChange={(e) => {
                      console.log(e.target.value)

                      changeFindItem(data.filter((v) => {
                        return v.nm.toLowerCase().includes(e.target.value)
                      }));

                    }}></input>


                    <div class="search_cancel_btn" onClick={() => {
                      changeSearchKeyword(!searchKeyword)
                    }}>취소</div>
                  </div>
                  {

                    findItem.length > 0 ?
                      <div className="row li_wrap">
                        <ul className="col-12 search_ul">
                          <SearchCard item={findItem} />
                        </ul>
                      </div>
                      : ''
                  }
                </div> : ''


            }


          </div>

          <div className="category_wrap">
            <ul onClick={(e) => {
              // console.log(e.target.parentElement.childNodes)
              e.target.parentElement.childNodes.forEach((v, i) => {
                // console.log(v)
                v.className = ''
              })

              if (e.target.className === 'active') {
                e.target.className = '';
              } else {
                e.target.className = 'active';
              }
            }}>
              <li className="active">오늘의 메뉴</li>
              <li>COFFEE</li>
              <li>NON COFFEE</li>
              <li>DESSERT</li>
            </ul>
          </div>

          <div>
            <img className="main_img" src={main}></img>
          </div>

          <div className="row li_wrap">
            <ul className="col-12">
              <Card item={item} />
            </ul>
          </div>
          
          <div className="footer">
            <div>이용약관 | 개인정보처리방침 | 고객센터</div>
            <div>Copyright Table order. All rights reserved.</div>
          </div>
        </Route>
        
        
        <Route path="/detail/:id">
          <Detail item={item}/>
        </Route>

      </Switch>

    </div>
  );
}

function Card(props) {

  return (
    props.item.map(function (a, i) {
      return (
        <Link to={`detail/${a.id}`}>
        <li key={i}>
          <div className="img_wrap">
            <img className="item_img" src={"https://raw.githubusercontent.com/jinitop/jinitop.github.io/main/item" + a.id + ".jpg"} />
          </div>
          <div className="contents_wrap">
            <div className="item_nm">
              <div>{a.nm}</div>
              {a.popular === 0 ? <div></div> : <div className="item_popular">인기</div>}
              {a.signature === 0 ? <div></div> : <div className="item_signature">대표</div>}
            </div>
            <div className="item_price">{a.pri}</div>
          </div>
        </li>
        </Link>
      )
    })
  )
}

function SearchCard(props) {

  return (
    props.item.map(function (a, i) {
      return (
        <Link to={`detail/${a.id}`}>
          <li className="search_li" key={i}>
            <div className="img_wrap">
              <img className="item_img" src={"https://raw.githubusercontent.com/jinitop/jinitop.github.io/main/item" + a.id + ".jpg"} />
            </div>
            <div className="contents_wrap02">
              <div className="item_nm">
                <div>{a.nm}</div>
                {a.popular === 0 ? <div></div> : <div className="item_popular">인기</div>}
                {a.signature === 0 ? <div></div> : <div className="item_signature">대표</div>}
              </div>
            </div>
          </li>
        </Link>
      )
    })
  )
}


export default App;
