/* eslint-enable */

// 라이브러리
import React, { useEffect, useState } from 'react';
import { Link, useHistory, useParams } from 'react-router-dom'

// 이미지
import back from './images/icon/back.png';
import share from './images/icon/share.png';
import radioOn from './images/icon/radio2.png';
import radioOff from './images/icon/radio1.png';
import checkOn from './images/icon/check2.png';
import checkOff from './images/icon/check1.png';
import plus from './images/icon/plus.png';
import minus from './images/icon/minus.png';

// css
import './Detail.scss';

// js


function Detail(props) {

    useEffect(() => {
        window.scrollTo(0, 0);
    }, []);

    let [cnt, changeCnt] = useState(0);

    let { id } = useParams();
    let findItem = props.item.find((item) => {
        return item.id == id
    })

    return (
        <div>
            <div className="item_dt_top">
                <div className="back_icon">
                    <Link to="/">
                        <img src={back} />
                    </Link>
                </div>
                <div className="share_icon">
                    <img src={share} />
                </div>
                <div>
                    <img className="item_dt_img" src={"https://raw.githubusercontent.com/jinitop/jinitop.github.io/main/item" + findItem.id + ".jpg"} />
                </div>
                <div className="item_dt_info">
                    <div className="item_dt_con text-left">{findItem.nm}</div>
                    <div className="item_dt_con text-right">{findItem.pri}</div>
                </div>
            </div>
            <div className="item_dt_mid">
                <div className="item_dt_select">
                    <div className="pt-4 pb-2">수량</div>
                    <div className="cnt_wrap">
                        <div className="cnt_minus" onClick={(e)=>{
                            cnt < 1 ? changeCnt(cnt) : changeCnt(cnt-1)
                        }}><img src={minus} /></div>
                        <div className="cnt">{cnt}</div>
                        <div className="cnt_plus" onClick={()=>{
                            changeCnt(cnt+1)
                        }}><img src={plus} /></div>
                    </div>
                </div>
                <div className="item_dt_select">
                    <div>Hot/Ice 선택</div>
                    <div className="pt-3">
                        <img className="select_icon" src={radioOn} />
                        <div>Hot</div>
                    </div>
                    <div className="pt-3">
                        <img className="select_icon" src={radioOff} />
                        <div>Ice</div>
                    </div>
                </div>
                <div className="item_dt_select">
                    <div>사이즈 선택</div>
                    <div className="pt-3">
                        <img className="select_icon" src={radioOn} />
                        <div>Reqular</div>
                    </div>
                    <div className="pt-3">
                        <img className="select_icon" src={radioOff} />
                        <div>Large</div>
                    </div>
                </div>
                <div className="item_dt_select">
                    <div>추가 선택</div>
                    <div className="pt-3">
                        <img className="select_icon" src={checkOn} />
                        <div>휘핑 추가</div>
                    </div>
                    <div className="pt-3">
                        <img className="select_icon" src={checkOff} />
                        <div>샷 추가</div>
                    </div>
                </div>
            </div>
            <div className="item_dt_bt">
                <div className="item_dt_price_div">
                    <div className="price_txt text-left">총 금액</div>
                    <div className="price bold">5,500원</div>
                </div>
                <div className="item_dt_btn_div">
                    <div className="menu_btn">메뉴담기</div>
                    <div className="order_btn">바로주문결제</div>
                </div>
            </div>



            {
                // console.log(findItem)
            }
        </div>
    )

}


export default Detail;